.coursesList{background-image:url("https://images.unsplash.com/photo-1461749280684-dccba630e2f6?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1169&q=80");background-repeat:no-repeat;background-size:100vw 70vh;padding-top:10vh;padding-bottom:100px}.courseListHead{padding:100px;text-align:center;color:#036bcb;margin:30px 20vw}.courseListHead h1{font-size:30px;font-weight:bolder}.contentWrap{background-color:#fff}.blog-card{display:flex;flex-direction:column;margin:1rem auto;box-shadow:10px 6px 14px -1px rgba(0,0,0,.4);margin-bottom:1.6%;background:#fff;line-height:1.4;font-family:sans-serif;border-radius:5px;overflow:hidden;z-index:0}.blog-card a{color:inherit}.blog-card a:hover{color:#5ad67d}.blog-card:hover .photo{transform:scale(1.3) rotate(3deg)}.blog-card .meta{position:relative;z-index:0;height:200px;min-width:210px}.blog-card .photo{position:absolute;top:0;right:0;bottom:0;left:0;background-size:cover;background-position:center;transition:transform .2s}.blog-card .photo img{width:100% !important;height:100% !important}.blog-card .details,.blog-card .details ul{margin:auto;padding:0;list-style:none}.blog-card .details{position:absolute;top:0;bottom:0;left:-100%;margin:auto;transition:left .2s;background:rgba(0,0,0,.6);color:#fff;padding:10px;width:100%;font-size:1.5rem}.blog-card .details a{text-decoration:dotted underline}.blog-card .details ul li{display:inline-block}.blog-card .details .author:before{font-family:FontAwesome;margin-right:10px;content:""}.blog-card .details .date:before{font-family:FontAwesome;margin-right:10px;content:""}.blog-card .details .tags ul:before{font-family:FontAwesome;content:"";margin-right:10px}.blog-card .details .tags li{margin-right:2px}.blog-card .details .tags li:first-child{margin-left:-4px}.blog-card .description{padding:1rem;min-width:325px;background:#fff;position:relative;z-index:1;animation:1.5s shine linear infinite}.blog-card .description h1,.blog-card .description h2{font-family:Poppins,sans-serif;background:#eee;background:linear-gradient(110deg, #ece9e9 8%, #ffffff 18%, #ececec 33%);border-radius:5px;background-size:200% 100%;animation:1.5s shine linear infinite}@keyframes shine{to{background-position-x:-200%}}.blog-card .description h1{line-height:1;margin:0;font-size:1.7rem}.blog-card .description h2{font-size:1rem;font-weight:300;text-transform:uppercase;color:#a2a2a2;margin-top:5px}.blog-card .description .read-more{text-align:right}.blog-card .description .read-more a{color:#5ad67d;display:inline-block;position:relative}.blog-card .description .read-more a:after{content:"";font-family:FontAwesome;margin-left:-10px;opacity:0;vertical-align:middle;transition:margin .3s,opacity .3s}.blog-card .description .read-more a:hover:after{margin-left:5px;opacity:1}.blog-card p{background:#eee;background:linear-gradient(110deg, #ece9e9 8%, #ffffff 18%, #ececec 33%);border-radius:5px;background-size:200% 100%;animation:1.5s shine linear infinite;position:relative;margin:1rem 0 0}@keyframes shine{to{background-position-x:-200%}}.blog-card p:first-of-type{margin-top:1.25rem}.blog-card p:first-of-type:before{content:"";position:absolute;height:5px;background:#5ad67d;width:35px;top:-0.75rem;border-radius:3px}.blog-card:hover .details{left:0%}@media(min-width: 640px){.blog-card{flex-direction:row;max-width:700px}.blog-card .meta{flex-basis:40%;height:auto}.blog-card .description{flex-basis:60%}.blog-card .description:before{transform:skewX(-3deg);content:"";background:#fff;width:30px;position:absolute;left:-10px;top:0;bottom:0;z-index:-1}.blog-card.alt{flex-direction:row-reverse}.blog-card.alt .description:before{left:inherit;right:-10px;transform:skew(3deg)}.blog-card.alt .details{padding-left:25px}}